@media screen and (min-width: 1200px) {
body {
   margin: 0;
   padding: 0;
   
   background-color: rgb(242, 241, 237);
   display: flex;
   justify-content: center;
   align-items: center;
}


.title_container {
   width: 500px;
   grid-area: title;
}

.title {
   font-family: "Mulish", sans-serif;
   font-weight: 700;
   font-size: 40px;

   padding: 0;
   margin: 0;
}

._h1-2 {
   font-size: 30px;
   text-align: center;
}
._h2 {
   padding-left: 20px;
   padding-top: 20px;
   font-size: 16px;
}

.text_center {
   padding: 0;
   margin: 0;
   text-align: center;
}

.component {
   display: grid;
   grid-template-columns: 622px 518px;
   grid-template-areas:
      "title title"
      "result _color_1"
      "result _color_2"
      "result finally";
   /* grid-template-rows: auto 189px 244px 93px; */
   grid-template-rows: auto 180px 180px 166px;
   gap: 20px;

   align-items: center;
   justify-content: center;
   padding: 30px 0;
   width: 1200px;
   background-color: rgb(242, 241, 237);
}

.container-result {
   grid-area: result;
   align-items: start;
   overflow: hidden;

   width: 622px;
   height: 566px;

   border-radius: 10px;




      background-color: #dc0c0c;
}

.image-content {
   justify-content: center;
   align-items: center;
   display: flex;
   overflow: hidden;

   width: 622px;
   /* height: 566px; */
}
.image-content img {
   align-items: center;
   overflow: hidden; 
   width: 622px;
   /* height: 566px; */
}


.container-color_1 {
   grid-area: _color_1;
   justify-content: center;
   width: 518px;
   /* height: 189px; */
   height: 180px;
   border-radius: 10px;

   background-color: #161616;
}
.container-color-colorhand {
   padding: 16px 20px 0 20px;
   display: grid;
   justify-content: center;
   grid-template-columns: repeat(9, 48px);
   gap: 6px;
}
._colors {
   height: 48px;
   width: 48px;
   border-radius: 10px;
   border: none;
}


.container-color_2 {
   grid-area: _color_2;
   width: 518px;
   /* height: 244px; */
   height: 180px;
   border-radius: 10px;

   background-color: #F2EAE3;
}

.container-finally {
   grid-area: finally;  
   height: 93px;
   align-items: center;
   justify-content: center;

   margin: 73px 0 0 0;
}
}

